import style from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import defaultUserPhoto from "../../../images/photo.jpg"

const ProfileInfo = ({profile, status, updateStatus}) => {
    if (!profile) {
        return <Preloader />
    }
    return (
        <div>
            <div>
                <img className={style.photo} src={profile.photos.large || defaultUserPhoto} alt="Avatar"/>
            </div>
            <div>
                <p>{profile.fullName}</p>
                <ProfileStatusWithHooks status={status}
                               updateStatus={updateStatus}/>
                <p>About Me:</p>
                <p>{profile.aboutMe}</p>
                <p>{profile.lookingForAJob ? "  " :"  "}</p>
                <p>{profile.lookingForAJobDescription}</p>
            </div>
            <div>
                <div>
                    vk: {profile.contacts.vk}
                </div>
                <div>
                    youtube: {profile.contacts.youtube ? "profile.contacts.youtube" : " "}
                </div>
            </div>
        </div>
    )
}

export default ProfileInfo;